import React, { Component } from 'react';
import { connect } from 'react-redux';
import './CheckBox.css';
import Select from '../../filterform/select/Select'
import { setSelectedInfluencersData } from '../../../../actions'

class CheckBox extends Component {

  constructor(props) {
    super(props);
    this.state = {
      influencerChecked: false
    }
  }

  // componentDidMount() {
  //   const { selectedInfluencersData, influencer_id } = this.props;
  //   const selectedInfluencersArray = selectedInfluencersData.split(",");
  //   for (let i = 0; i < (selectedInfluencersArray.length - 1); i++) {
  //     if (selectedInfluencersArray[i] === influencer_id) {
  //       console.log("matched");
  //       let checkboxName = "Influencerid-" + influencer_id;
  //       document.getElementsByName(checkboxName).checked = true;
  //     }
  //   }
  // }



  async ChangeHandler() {

    const { selectedInfluencersData, influencer_id } = this.props;

    await this.setState(prevState => ({
      influencerChecked: !prevState.influencerChecked
    }));

    let UpdatedSelectedInfluencersData = selectedInfluencersData;
    if (this.state.influencerChecked) {
      UpdatedSelectedInfluencersData += influencer_id + ",";
      console.log("true conditon ", UpdatedSelectedInfluencersData);
      this.props.setSelectedInfluencersData(UpdatedSelectedInfluencersData);
    } else {
      if (UpdatedSelectedInfluencersData.includes(influencer_id)) {
        const replacingString = influencer_id + ",";
        UpdatedSelectedInfluencersData = UpdatedSelectedInfluencersData.replace(replacingString, " ");
        console.log("false condition ", UpdatedSelectedInfluencersData);
        this.props.setSelectedInfluencersData(UpdatedSelectedInfluencersData);
      }
    }
  }

  render() {
    let classes = ["regular-checkbox", this.props.partOf];
    let checkboxName = "Influencerid-" + this.props.influencer_id;
    return (
      <div className={this.props.outerDiv} >
        <input type="checkbox" name={checkboxName} className={classes.join(" ")} onChange={() => this.ChangeHandler()} />
      </div>
    );
  }
}

const mapStateToProps = state => ({
  selectedInfluencersData: state.selectedInfluencersData
});
const mapDispatchToProps = dispatch => {
  return {
    setSelectedInfluencersData: selectedInfluencersData => {
      dispatch(setSelectedInfluencersData(selectedInfluencersData));
    }
  }
}

export default connect(
  mapStateToProps, mapDispatchToProps
)(CheckBox);