import React, { Component } from 'react';
import { connect } from 'react-redux';


import { addInfluencersToGroups, addInfluencersGroup } from "../../../../actions";

class AssignToListButton extends Component {

    clickHandler = (userCredentials) => {
        var listSelected = document.getElementById("assignLists").value;
        if (listSelected.includes("Create New List")) {
            listSelected = listSelected.replace("Create New List", "");
            console.log("Contains Create ", listSelected);
            this.props.addInfluencersGroup(userCredentials);
        }
        if (listSelected !== "") {
            const influencersListUserInfo = {
                influencersListInfo: {
                    list_names: listSelected,
                    influencer_ids: this.props.selectedInfluencersData,
                    action: "ADD"
                },
                userCredentials: userCredentials
            }
            this.props.addInfluencersToGroups(influencersListUserInfo);
        }
    }
    renderButton = () => {
        if (this.props.userCredentials === "") {
            return null;
        } else {
            console.log("inside render Button ", this.props.userCredentials);
            return <button onClick={() => { this.clickHandler(this.props.userCredentials) }}>Assign</button>
        }
    }
    render() {
        return <span>
            {this.renderButton()}
        </span>

    }
}
const mapStateToProps = state => ({
    selectedInfluencersData: state.selectedInfluencersData,
    userCredentials: state.userCredentials
});

const mapDispatchToProps = dispatch => {
    return {
        addInfluencersToGroups: influencersListUserInfo => {
            dispatch(addInfluencersToGroups(influencersListUserInfo));
        },
        addInfluencersGroup: userCredentials => {
            dispatch(addInfluencersGroup(userCredentials));
        }
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(AssignToListButton);